import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'Cronopoly',
  description:
    'The most popular AMM on BSC by user count! Earn CRP through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by Cronopoly), NFTs, and more, on a platform you can trust.',
 
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  let basePath
  if (path.startsWith('/swap')) {
    basePath = '/swap'
  } else if (path.startsWith('/add')) {
    basePath = '/add'
  } else if (path.startsWith('/remove')) {
    basePath = '/remove'
  } else if (path.startsWith('/teams')) {
    basePath = '/teams'
  } else if (path.startsWith('/voting/proposal') && path !== '/voting/proposal/create') {
    basePath = '/voting/proposal'
  } else {
    basePath = path
  }

  switch (basePath) {
    case '/':
      return {
        title: `${t('Home')} | ${t('Cronopoly')}`,
      }
    case '/swap':
      return {
        title: `${t('Exchange')} | ${t('Cronopoly')}`,
      }
    case '/add':
      return {
        title: `${t('Add Liquidity')} | ${t('Cronopoly')}`,
      }
    case '/remove':
      return {
        title: `${t('Remove Liquidity')} | ${t('Cronopoly')}`,
      }
    case '/liquidity':
      return {
        title: `${t('Liquidity')} | ${t('Cronopoly')}`,
      }
    case '/find':
      return {
        title: `${t('Import Pool')} | ${t('Cronopoly')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('Cronopoly')}`,
      }
    case '/farms/auction':
      return {
        title: `${t('Farm Auctions')} | ${t('Cronopoly')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('Cronopoly')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('Cronopoly')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('Cronopoly')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | ${t('Cronopoly')}`,
      }
    case '/info':
      return {
        title: `${t('Overview')} | ${t('Cronopoly Info & Analytics')}`,
        description: 'View statistics for Cronopoly exchanges.',
      }
    case '/info/pools':
      return {
        title: `${t('Pools')} | ${t('Cronopoly Info & Analytics')}`,
        description: 'View statistics for Cronopoly exchanges.',
      }
    case '/info/tokens':
      return {
        title: `${t('Pools')} | ${t('Cronopoly Info & Analytics')}`,
        description: 'View statistics for Cronopoly exchanges.',
      }
    default:
      return null
  }
}
